/* ------------------------------*/
.reservation-button {
    position: absolute;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #ffffff;
    padding-left: 35px;
    
    /* PC-l 1367px~  */
    @include foundation_mixin.pc(1367px) {
        font-size: 16px;
        right: 20px;
    }
    /* PC 1025~1366px */
    @include foundation_mixin.pc(1367px) {
        font-size: 16px;
        right: 20px;
    }
    /* TAB 600~1024px */
    @include foundation_mixin.pc(1025px) {
        font-size: 14px;
        right:50px;
    }
    /* SP 375~599px */
    @include foundation_mixin.pc(600px) {
        font-size: 14px;
        
        width: 110px !important;
        height: 50px !important;

        right:15%;

    }
}

.reservation-btn-icon {
    position: absolute;
    
    /* PC-l 1367px~  */
    @include foundation_mixin.pc(1367px) {
            transform: translate(-55px, -2px);
            
        }
        /* PC 1025~1366px */
        @include foundation_mixin.pc(1367px) {
            transform: translate(-55px, -2px);
        }
        /* TAB 600~1024px */
        @include foundation_mixin.pc(1025px) {
        }
        /* SP 375~599px */
        @include foundation_mixin.pc(600px) {
            transform: translate(-45px, -2px);

         
        }
}

